<template lang="html">
  <div class="article">
    <h1 class="title">{{ article.title }} <span>{{ article.updated_at.substring(0, 10) }}</span></h1>
    <div class="markdown-body">
      {{{ article.body | markdown }}}
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      id: '',
      article: {
        title: '',
        updated_at: '',
        body: ''
      }
    }
  },
  props: ['articles'],
  watch: {
    'articles': function (val) {
      let data = val.find(item => item.id.toString() === this.id)

      if (data) {
        this.article = data
      }
    }
  },
  route: {
    data () {
      this.id = this.$route.params.id || ''

      let data = this.articles.find(item => item.id.toString() === this.id)

      if (data) {
        this.article = data
      }
    }
  }
}
</script>

<style lang="scss">
@import "../style/css/github2.css";
@import "../style/css/hljs.github.css";

.article {
  .title {
    padding: 12px 0;
    border-bottom: 1px solid #ccc;

    span {
      float: right;
      line-height: 46px;
      font-size: 16px;
      font-weight: normal;
    }
  }
}
</style>



