<template lang="html">
  <div class="article-list">
    <h1>{{ label || '首页' }}</h1>
    <ul>
      <li v-for="article in data">
        {{ article.updated_at.substring(0, 10) }}
        <a v-link="{ name: 'article', params: { id: article.id } }">{{ article.title }}</a>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data () {
    return {
      label: '',
      data: []
    }
  },
  props: ['articles'],
  watch: {
    'articles': function (val) {
      this.getByLabel(val)
    }
  },
  methods: {
    getByLabel (val) {
      let data = val || []

      if (this.label) {
        data = data.filter(item => {
          return item.labels.some(it => it.name === this.label)
        })
      }

      this.data = data
    }
  },
  route: {
    data () {
      this.label = this.$route.params.label || ''

      this.getByLabel(this.articles)
    }
  }
}
</script>

<style lang="scss">
.article-list {
  ul li {
    line-height: 1.8;
  }
}
</style>



