<template lang="html">
  <div id="BODY_WRAP">
    <sidebar :labels="labels"></sidebar>

    <div id="VIEWS">
      <router-view
        :articles="articles"
        keep-alive>
      </router-view>
    </div>

    <bottom></bottom>
  </div>
</template>

<script>
export default {
  data () {
    return {
      articles: [],
      labels: []
    }
  },
  components: {
    // Top: require('./components/top.vue'),
    Sidebar: require('./components/sidebar.vue'),
    Bottom: require('./components/bottom.vue')
  },
  ready () {
    this.$http.get('https://api.github.com/repos/ansenhuang/ansenhuang.github.io/issues?sort=updated')
      .then(response => {
        if (response.status === 200) {
          let data = response.data || []
          let labels = []

          data.forEach(item => {
            item.labels.forEach(it => {
              if (labels.every(label => label.name !== it.name)) {
                labels.push(it)
              }
            })
          })

          this.articles = data
          this.labels = labels
        }
      }, response => {
        console.log(response.error || '数据请求失败')
      })
  }
}
</script>

<style lang="scss">
@import "./style/scss/reset";

#BODY_WRAP {
  position: relative;
  height: 100%;
  margin: 20px 40px;
}

#VIEWS {
  margin-left: 18%;
  min-height: 100%;
}
</style>



